import "./styles.css";
import React from "react";
import ContextData from "./ContextData";

export const SampleReactContext = React.createContext({
  theme: "dark",
  language: "en"
});
SampleReactContext.displayName = "Application Context";

export default function App() {
  const [appTheme, setAppTheme] = React.useState("light");
  return (
    <SampleReactContext.Provider
      value={{
        theme: appTheme,
        language: "en"
      }}
    >
      <div
        className="theme-button"
        onClick={(event) => {
          event.stopPropagation();
          setAppTheme(appTheme === "light" ? "dark" : "light");
        }}
      >
        <span>{appTheme === "dark" ? "ðŸ”†" : "ðŸŒ™"}</span>
      </div>
      <ContextData />
    </SampleReactContext.Provider>
  );
}
